export class WavWriter{
    private audioData: any[];

    /**
     * @param {Object} config {channels: 1, sampleSizeInBits: 16, sampleRate: 16000}
     */
    constructor(private config: {[key: string]: any} = {}){
        this.config = {channels: 1, sampleSizeInBits: 16, sampleRate: 16000, ...config};
        this.audioData = [];
    }

    /**
     * @param {ArrayBuffer} data 
     */
    addData(data: ArrayBuffer){
        this.audioData.push(data);
    };

    /**
     * @return {ArrayBuffer}
     */
    getWavFile(){
        const dataSize = this.audioData.map(d=>d.byteLength).reduce((l, r)=>l+r);
        const buffer = new ArrayBuffer(44 + dataSize);
        var view = new DataView(buffer);

        const writeString = function(view: DataView, offset: number, string: string) {
            for (let i = 0; i < string.length; i++){
                view.setUint8(offset + i, string.charCodeAt(i));
            }
        };
        writeString(view, 0, 'RIFF');  // RIFF
        view.setUint32(4, 32 + dataSize, true); // 
        writeString(view, 8, 'WAVEfmt '); // WAVE
        view.setUint32(16, 16, true); // fmt
        view.setUint16(20, 1, true); // ID
        view.setUint16(22, this.config.channels, true); // 
        view.setUint32(24, this.config.sampleRate, true); // 
        view.setUint32(28, this.config.sampleRate * 2, true); // 
        view.setUint16(32, 2, true); // 
        view.setUint16(34, this.config.sampleSizeInBits, true); // 
        writeString(view, 36, 'data'); // data
        view.setUint32(40, dataSize, true); // 
        const dst = new Uint8Array(buffer, 44);
        let index = 0;
        for(let s of this.audioData){
            // Uint8Arraymemcpy
            dst.set(new Uint8Array(s), index);
            index += s.byteLength;
        }
        console.debug(`${buffer.byteLength} bytes file created.`);
        return buffer;
    };
}
